import App from "./App"
import Home from './Home';
export default function Main() {
  return (
<BrowserRouter>
	<Routes>
        <Route exact path="/" element={<App/>}>
          <Route exact path='/home' element={<Home/>}/>
        </Route>
       </Routes>
    </BrowserRouter>
  );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<Routes />);

